const videoResSet = [
    { label: 'full_hd', width: 1920, height: 1080, frameRate: 30, priority: 0 },
    { label: 'hd', width: 1280, height: 720, frameRate: 30, priority: 1 },
    { label: '640', width: 640, height: 360, frameRate: 30, priority: 2 },
    { label: '480', width: 640, height: 480, frameRate: 30, priority: 3 },
    { label: '320', width: 320, height: 180, frameRate: 30, priority: 4 },
    { label: '240', width: 320, height: 240, frameRate: 30, priority: 5 }
].sort((a, b) => a.priority - b.priority);

export const checkVideoCapability = async deviceId => {
    const result = [];

    for (const res of videoResSet) {
        try {
            const stream = await navigator.mediaDevices.getUserMedia({
                audio: false,
                video: {
                    width: { exact: res.width },
                    height: { exact: res.height },
                    frameRate: { exact: res.frameRate },
                    deviceId: { exact: deviceId }
                }
            });
            const capability = stream.getVideoTracks()[0].getCapabilities();
            // console.log(capability);
            result.push({
                ...res,
                available: true,
                capability
            });
        } catch (err) {
            result.push({ ...res, available: false });
        }
    }
    return result;
};

export const checkAudioCapability = async deviceId => {
    try {
        const stream = await navigator.mediaDevices.getUserMedia({
            audio: {
                deviceId: {
                    exact: deviceId,
                },
                echoCancellation: true,
                noiseSuppression: true
            },
            video: false
        });
        const capability = stream.getAudioTracks()[0].getCapabilities();
        return {
            ...capability,
            available: true,
            echoCancellation: true,
            noiseSuppression: true
        };
    } catch (err) {
        return { available: false };
    }
};

export const getAllDevices = async () => {
    const devices = await navigator.mediaDevices.enumerateDevices();
    const videoDevices = [];
    const audioDevices = [];

    for (const device of devices) {
        // audiooutput은 무시한다.
        if (device.kind !== 'videoinput' && device.kind !== 'audioinput')
            continue;

        if (device.kind === 'videoinput') {
            videoDevices.push({
                id: device.deviceId,
                name: device.label || `CAM ${videoDevices.length}`,
                kind: device.kind
            });
        } else {
            audioDevices.push({
                id: device.deviceId,
                name: device.label || `MIC ${audioDevices.length}`,
                kind: device.kind
            });
        }
    }
    return [...videoDevices, ...audioDevices];
};

export const getAllCheckedDeviceList = async () => {
    const resultArr = [];

    const allDevices = await getAllDevices();
    for (const device of allDevices) {
        const { id, name, kind } = device;
        const capability =
            kind === 'videoinput'
                ? await checkVideoCapability(id)
                : await checkAudioCapability(id);

        resultArr.push({
            id,
            name,
            ...capability
        });
    }
    return resultArr;
};

export const getDisplayStream = () => {
    return navigator.mediaDevices.getDisplayMedia({
        video: true
    });
};

export const getDefaultAudio = () => {
    return navigator.mediaDevices.getUserMedia({
        video: false,
        audio: {
            noiseSuppression: true,
            echoCancellation: true
        }
    });
};

class DisplayDevice {
    constructor() {
        this.currentStream = null;
    }

    async init() {
        await this._setDisplayDevice();
    }

    async _setDisplayDevice() {
        try {
            const displayStream = await getDisplayStream();
            const audioStream = await getDefaultAudio();

            this.currentStream = new MediaStream();
            this.currentStream.addTrack(displayStream.getVideoTracks()[0]);
            this.currentStream.addTrack(audioStream.getAudioTracks()[0]);
        } catch (err) {
            this.currentStream = null;
            alert($.i18n.t('wrng_err_n_try'));
            throw new Error(err);
        }
    }

    get stream() {
        return this.currentStream;
    }
}
const display = new DisplayDevice();
export default display;
