  return new Promise((resolve, reject) => {
    confirm(
      i18next.t('wrng_multistream_bf_start'),
      () => {
        resolve(true);
      },
      () => {
        resolve(false);
      }
    );
  })
  // 아래코드는 멀티방송 세팅이 아얘 안되어있을 때
